/* GLOBAL CSS */
h1 {
  font-size: 1.8em;
}
h2 {
  font-size: 1em;
}
h3 {
  font-size: 1.2em;
}
a {
  color: black;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

/* Header styles */
nav div {
  display: flex;
  justify-content: center;
}
.container {
  max-width: 860px;
}

/* Footer styles */
.bx-restaurant {
  font-size: 150px;
  float: right;
}
.copyright {
  margin: 0;
  padding: 0;
}
.icon-container {
  display: flex;
}
.navbar-brand {
  flex: 1 1 auto;
  align-items: end;
  font-size: 35px;
  color: gray;
  /* padding-bottom: 16px; */
}

/* Popover CSS */
.index-popovers {
  --bs-popover-header-bg: rgb(100, 185, 93);
  --bs-popover-bg: rgb(204, 255, 199);
}

/* General div stule for the carousel, 
has to be applied togehter with the id */
.carousel-image-container {
  height: 500px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.index-image-container {
  height: 400px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

